  /* styles.css */

  /* Add global styles here */

  body {
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
      margin: 0;
      padding: 0;
      display: flex;
      flex-direction: column;
      min-height: 100vh; /* Make sure the body takes up at least the full height of the viewport */
    }
    
    header {
      
      font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
      background-color: #00bfff;
      color: rgba(0, 0, 0, 0.427);
      padding: 10px;
      /* text-align: center;   */
      display: flex;
      justify-content: space-between;
      align-items: center;

    }
    
    
    .header p {
      margin-left: auto; /* This will push the paragraph to the right */
    }

  
    .header-btn{
      color: #ffffff;
    }
    
    header h1 {
      margin: 0;
    }
    
    
    nav {
      background-color: rgb(128, 0, 255);
      padding: 10px;
    }
    
    .tabs {
      display: flex;
      /* justify-content: space-around; */
      /* Smooth transition for box-shadow */
      margin-left: 30%;
    }
    
    .tab {
      margin-left: 20px;
      background-color: #ffffff;
      color: #aa00ff;
      border: none;
      padding: 10px 20px;
    
      transition: transform 0.25s ease-in-out;
    }
    
    .tab:hover {
      background-color: #bbb;
     
    }
    
    main {
      padding: 20px;
      flex-grow: 1; /* Allow main content to grow to fill remaining space */
    }
    
   
    